* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
    font-size: 62.5%;

    @media all and (min-width: 768px) {
        font-size: 80%;
    }

    @media all and (min-width: 1024px) {
        font-size: 90%;
    }
}

.body-of-body{
    position: relative;
    //width: 100vw;   
    height: 100vh;
    z-index: 0;
}

.main {
    width: 100%;
    padding: 1rem 1rem 1rem 6rem;
    //height: 100%;
    // position: absolute;
    // z-index: 1;
    display: grid;
    //align-items: center;
    justify-items: center;
    // display: flex;
    // justify-content: center;
    // flex-direction: column;
    // align-items: center;

    @media all and (min-width: 768px) {
        padding: 3rem 5rem 3rem 10rem;
    }

    @media all and (min-width: 1024px) {
        padding: 3rem 10rem 3rem 15rem;
    }
}